<template>
    <div v-cloak class="card">
        <div class="image-frame">
            <div class="image-wrap">
                <a href="#" class="clickable-image">
                    <img class="item-image" :src="card.url" :alt="card.alt">
                </a>
            </div>
        </div>
        <div class="card-body">
            <div class="name-wrap"><a href="" class="item-name btn">{{card.name}}</a></div>
            <b class="item-price">{{card.price}}</b><br>
            <div class="item-rating heart-rating" :title="card.rating">
                <div class="back-hearts">     
                    <i v-for="index in 5" :key="index" class="fa fa-heart " :id="index"></i>
                </div>
                <div class="front-hearts">     
                    <i v-for="index in 5" :key="index" class="fa fa-heart " v-if="checked(index, card.rating)" :id="index"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: ["card"],

  data() {
    return {
    };
  },

  methods: {
      checked(index, percent){
        let rating = parseFloat(percent);     
        let checked = false;
        if( index <= 1+rating/20){
            checked = true;
        }
        return checked;
      }
  }
};
</script>

<style>
#section-b .card-body {
    width: 7rem;
    margin: 0 auto;
    text-align: center;
}

#section-b .heart-rating {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
}

#section-b .fa-heart {
    margin: .2rem;
    float: left;
}

#section-b .front-hearts {
    color: red;
    grid-row: 1/2;
    grid-column: 1/2;
}

#section-b .back-hearts{
    grid-row: 1/2;
    grid-column: 1/2;
}


</style>





