<template>   
    <main>
        <div class="main-wrap">
            <!-- Section A -->
            <section id="section-a">
                <div class="content-wrap bg-image" id="a.bg">
                    <div class="content-body grid">
                        <div class="desktop-only content-text">
                            <p>Encourage the joy of learning and <br>discovery through play.</p>
                        </div>
                        <a href="#" class="shop-now btn">Shop Now</a>
                        <div class="dots">
                            <i v-for="dot in 3" :key="dot" @click="changeSlide(dot)" class="dot" :class="activateDot(dot)">&#9679;</i>
                        </div>
                    </div>
                </div>       
                <div class="content-text mobile-only">
                    <p><b>Encourage the joy of learning and<br>discovery through play.</b></p>
                </div>       
            </section>
            <!-- Section B -->
            <section id="section-b">
                <!-- Best Sellers -->
                <div class="content-wrap best-sellers">
                    <div class="content-body">
                        <h2 class="content-title">BEST SELLERS</h2>
                        <!-- Slide Show -->
                        <slide-show class="mobile-only" slides=1></slide-show>
                        <slide-show class="desktop-only" slides=4></slide-show>
                        <!-- Slide Show Ends-->
                    </div>
                </div>
            </section>
            <!-- Section C -->
            <section id="section-c">
                <div class="content-wrap feature-toys">
                    <div class="content-body">
                        <h2 class="content-title">FEATURE TOYS</h2>
                        <div class="customize-toys">
                            <video class="customize-video" controls>
                            </video>
                            <div class="video-content">
                                <h3 class="video-title">Turn Your Art Into Real</h3>
                                <a href="#" class="video-btn btn">Customize Now</a>
                            </div>
                        </div>                   
                        <div class="card">
                            <img src="https://i.pinimg.com/originals/f9/08/cd/f908cd25a268eec4bb0e072e6aa4d70c.jpg" alt="Educational Toy">
                            <div class="card-content">
                                <h3 class="card-title">Gain Play Value</h3>
                                <p class="card-text desktop-only">A toy with good play value has lasting appeal, teaches numerous skills and encourages exploration.</p>
                                <a href="#" class="card-btn btn">Shop Educational Toys</a>
                            </div>
                        </div>                   
                        <div class="card opposite-card">
                            <img src="https://i.ytimg.com/vi/2qFDUhrb_7c/maxresdefault.jpg" alt="Eco-Friendly Toy">
                            <div class="card-content">
                                <h3 class="card-title">Eco-Friendly<br>Play With Nature</h3>
                                <p class="card-text desktop-only">We have a large selection of products made from sustainable resources and earth-friendly materials.</p>
                                <a href="#" class="card-btn btn">Shop Eco Toys</a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <!-- Section D -->
            <section id="section-d">
                <!-- Visit Us -->
                <div class="content-wrap">
                    <div class="content-body grid">
                        <div class="image-wrap mobile-only">
                            <img src="https://bit.ly/2lHEK3h" alt="">
                        </div>
                        <div class="card">
                            <div class="visit-us">
                                <h3 class="content-title">Visit Us and<br>Join Events</h3>
                                <a href="" class="fa fa-map-marker"></a>
                                <a href="" class="store-address"> 581 Danforth Avenue<br>
                                    Toronto, Ontario M4K 1PG
                                </a>
                                <p class="content-text desktop-only"><b>Upcoming Event:</b><br>
                                    Storytime<br>
                                    April 1- 5, 2018
                                </p>
                            </div>
                            <div class="sign-in">
                                <ul class="social-media-icons">
                                    <li>
                                        <a href="#" class="fa fa-facebook btn"></a>
                                    </li>
                                    <li>
                                        <a href="#" class="fa fa-twitter btn"></a>
                                    </li>
                                    <li>
                                        <a href="#" class="fa fa-google-plus btn"></a>
                                    </li>
                                    <li>
                                        <a href="#" class="fa fa-envelope btn"></a>
                                    </li>
                                </ul>
                                <div class="in-site">
                                    <a href="#" class="sign-up btn">Sign Up</a>
                                    <a href="#" class="log-in btn">Log In</a>
                                </div>
                            </div>                            
                        </div>
                    </div>
                </div>
            </section>
        </div>
    </main>   
</template>

<script>
import SlideShow from './SlideShow.vue';

export default {
    data() {
        return {
            bgLinks: [
                {url: "https://bit.ly/2tO1ZMS"},
                {url: "https://bit.ly/2Kilx6Y"},
                {url: "https://bit.ly/2N8v4LH"},       
            ],

            activeDot: 1,
            intervalId: "",    

        }
      
    },

    methods: {

        slideRight() {         
            let i = this.activeDot;
            if (i === this.bgLinks.length){
                i = 1;
            } else {                
                i++;
            }
            this.changeSlide(i);
        },

        changeSlide(i){
            this.activeDot = i;
            document.getElementById("a.bg").style.backgroundImage = 'url('+ this.bgLinks[i-1].url +')';           
        },

        activateDot(i) {
            if(i === this.activeDot){
                return "active";
            } else {
                return "";
            }           
        },

        autoChange(){    
            const self = this;   
            this.intervalId = setInterval(self.slideRight, 5000);
        },

        stopChange(){
            clearInterval(this.intervalId);
        }
    },

    mounted() {
        this.autoChange();
    },

    destroyed() {
        this.stopChange();
    },

    components: { 
        'slide-show': SlideShow,
    },
}
</script>

<style>
</style>






