<script>

export default {
  data() {

    return {      
      showMenu: false,

      navLinks: [
        {route : "/ToyShopResponsive/index.html",
        tag: "Home",
        id: 0
        },

        {route : "/ToyShopResponsive/incomplete/category",
        tag: "Category",
        id: 1
        },

        {route : "/ToyShopResponsive/incomplete/brand",
        tag: "Brand",
        id: 2
        },

        {route : "/ToyShopResponsive/incomplete/age",
        tag: "Age",
        id: 3
        },

        {route : "/ToyShopResponsive/incomplete/book",
        tag: "Books",
        id: 4
        },

        {route : "/ToyShopResponsive/incomplete/customize",
        tag: "Customize",
        id: 5
        },
      ]
    };
  },

  methods: {
    activate(event) {
      let parent = event.target.parentElement;
      parent = parent.parentElement;
      let btnNodes = parent.getElementsByClassName("btn");
      let buttons = Array.prototype.slice.call(btnNodes);

      buttons.forEach(button => {
        button.activeness = "btn";
      });
      event.target.activeness = "active btn";
    },

    toggle() {
      this.showMenu = !this.showMenu;  
    },
  },
}
</script>


