  .customize-toys .video-content {
    grid-column: 2/5;
    grid-row: 3/5;
  }

  #section-c .card {
    display: grid;
    grid-template-columns: 1fr 0.1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    height: 15rem;
  }

  #section-c .card img {
    grid-column: 1/2;
    grid-row: 1/4;
    height: 100%;
  }

  #section-c .card .card-content {
    padding: 0 1em;
    grid-column: 3/4;
    grid-row: 2/3;
  }

  #section-c .opposite-card .card-content {
    grid-column: 1/2;
  }

  #section-c .opposite-card img {
    grid-column: 3/4;
  }

  /* ------------- Section D / Visit Us - Sign In ------------- */

  #section-d {
    margin: 0;
  }

  #section-d .content-wrap {
    padding-bottom: 0;
    background: rgba(51, 51, 51, 0.069)
      url("https://bit.ly/2lHEK3h");
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    padding: 2vw 0;
  }

  #section-d .content-body {
    background: none;
    grid-template-rows: repeat(12, 1fr);
    height: 45rem;
  }

  #section-d .card {
    padding: 2rem 0;
    background-color: rgba(213, 213, 213, 0.756);
    border-radius: 2rem;
    grid-column: 9/12;
    grid-row: 5 / auto;
    grid-auto-rows: auto;
  }

  #section-d .social-media-icons {
    margin: 0;
  }

  #section-d .social-media-icons .btn {
    margin: 0.2rem 0.1rem;
    padding: 0.4rem;
    height: 1.2rem;
    width: 1.2rem;
  }

  #section-d .in-site .btn {
    border-radius: 0.5rem;
    color: white;
    padding: 0.5rem;
    margin: 0.5rem;
    font-weight: bold;
  }

  /*-------------- Footer -------------*/

  /* ------------- Epilogue ------------- */

  #epilogue .content-wrap {
    display: flex;
    justify-content: center;
  }

  #epilogue .content-body {
    padding: 0.2em 3em;
  }

  #epilogue .content-body * {
    color: #3a71aa;
    text-align: left;
    padding: 0;
    margin: 0;
  }

  #epilogue .content-body li * {
    padding: 0.4rem;
  }

  #epilogue ul {
    display: flex;
    justify-content: space-between;
  }

  #epilogue li {
    display: flex;
    flex-direction: column;
    width: 10rem;
  }

  #epilogue .help {
    width: 6rem;
  }

  #epilogue .nav-wrap {
    background-color: #3a71aa;
  }

  #epilogue .nav-wrap a {
    color: rgba(255, 255, 255, 0.85);
  }

  #epilogue .secondary-nav-wrap {
    background-color: #a9dae6;
  }

  #epilogue .btn {
    border: none;
  }

  #epilogue .copyright-wrap {
    padding-top: 1em;
    padding-bottom: 1em;
    background-color: #a9dae6;
  }

  #epilogue .copyright {
    font-weight: bold;
    padding-left: 0.4em;
  }
}

@media only screen and (min-width: 850px) {
  html {
    font-size: 15px;
  }
}
</style>






