<template>
    <div class="slide-show grid">
        <a class="control btn btn-left" @click="slideLeft">&#10094;</a>
        <ul class="content-body card-list">
            <li v-for="(crd, index) in cards" :key="index" v-if="slideCount(index)">           
                <item-card :card="crd"></item-card>
            </li>
        </ul>
        <a class="control btn btn-right" @click="slideRight">&#10095;</a>                       
    </div>
</template>

<script>

import Card from './Card.vue';

export default {
    props: ['slides'],

    data(){
        return {
            cards: [
                {url: "https://gdurl.com/7OyL",
                name: "Balance Bike",
                alt: "Balance Bike",
                price: "$39.99",
                rating: "90%",                
                id: 0
                },
                
                {url: "https://bit.ly/2twAeIp",
                name: "Green Truck",                
                alt: "Green Truck",
                price: "$19.99",
                rating: "60%",
                id: 1
                },

                {url: "https://bit.ly/2K3osAD",
                name: "Softy Bear",
                alt: "Softy Bear",
                price: "$59.99",
                rating: "70%",
                id: 2
                },

                {url: "https://bit.ly/2Ki8liv",
                name: "Rubics Cube",              
                alt: "Rubics Cube",
                price: "$13.99",
                rating: "30%",
                id: 3
                },

                {url: "https://bit.ly/2MqsnEf",
                name: "Best Toy",                
                alt: "Tribute",
                price: "$19.99",
                rating: "99%",
                id: 4
                },
            ],

        };      
    },
   
    components: {
        'item-card': Card,
    },

    methods: {
        slideLeft() {
            const bottom = this.cards.shift();
            //bottom goes top
            this.cards.push(bottom);
            console.log(this.cards[0].name);                      
        },

        slideRight() {
            const top = this.cards.pop();
            //top goes bottom
            this.cards.unshift(top);  
        },

        slideCount(i) {
            if(0 <= i && i < this.slides ){                
                return true;
            } else {
                return false;
            }
        }
    },
}
</script>

<style>

</style>







