import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './App.vue';
import Incomplete from './Incomplete.vue';
import Home from './Home.vue';

Vue.use(VueRouter);

const routes = [
  { path: '/ToyShopResponsive/incomplete/:title', component: Incomplete },
  { path: '/ToyShopResponsive/index.html', component: Home }
];

const router = new VueRouter ({
  routes,
  mode: 'history'
});

new Vue({
  el: '#app',
  router,
  render: h => h(App)
});



